.confirm-bg {
  padding-top: 70px;
  padding-bottom: 70px;
}

.confirm-page {
  grid-column: 3 / 11;
  grid-row: 2;
}

@media screen and (max-width: 768px) {
  .confirm-page {
    grid-column: 1 / 13;
    padding: 32px;
  }
}

@media only screen and (max-width: 375px) {
  .confirm-page {
    grid-column: 1 / 13;
    padding: 16px;
  }
}
